import React from 'react';
import styles from './PlusUpSimul.scss';
import classNames from 'classnames/bind';
import NumberFormat from 'react-number-format';
import Adsense72890 from 'components/adsense/Adsense72890';
import Adsense320100 from 'components/adsense/Adsense320100';

const cx = classNames.bind(styles);

const PlusUpSimul = ({setPlusUpKit, plusUpSimul, plusUpGo, plusUpGoF, plusUpGoKey, plusUpGoKeyF, currPlusDmg, plusUpKitFNeed, plusUpKitNeed}) => {
  const {
    usePlusUpKit, usePlusUpKitF, usePlusUpKitEp, upTryCnt, simulCurrPlusUp, bestPlusUp, resultComment
  } = plusUpSimul;

  return (
    <div className={cx('plusup-simul')}>      
      <div className={cx('kit-trade')}>
        <div className={cx('kit-trade-title')}>
          <div className={cx('kit-trade-left')}>  </div>
          <div className={cx('kit-trade-center')}>
          </div>
          <div className={cx('kit-trade-right')}>
            <button onClick={() => setPlusUpKit('0')}><br/></button>
            <button onClick={() => setPlusUpKit('1')}> 10Set<br/>1</button>
            <button onClick={() => setPlusUpKit('10')}> 10Set<br/>10</button>
            <button onClick={() => setPlusUpKit('100')}> 10Set<br/>100</button>
            <button onClick={() => setPlusUpKit('1000')}> 10Set<br/>1000</button>
          </div>
        </div>
        <div className={cx('plusup-notice')}>
          <div className={cx('notice-left')}>{upTryCnt} </div>
          <div className={cx('notice-center')}>
            <div className={cx('notice-center-top')}>{resultComment}</div>
            <div className={cx('notice-center-bottom')}>(): +{bestPlusUp}</div>
          </div>
          <div className={cx('notice-right')}>
            <NumberFormat value={usePlusUpKitEp} displayType={'text'} thousandSeparator={true} prefix={'EP: '} suffix ={'EP'}/>
          </div>
        </div>
        <div className={cx('adsense')} style={{paddingTop: '0.4rem'}}>
          <Adsense72890/>
          <Adsense320100/>
        </div>
        <div className={cx('kit-trade-body')}>
          <div className={cx('kit-use')}>
            <div className={cx('kit-use-normal')} onClick={plusUpGo} tabIndex='1' onKeyPress={plusUpGoKey}>
              <div></div>
              <img src='/resource/img/plusupkit.gif' alt=''/>
              <div><b><NumberFormat value={usePlusUpKit} displayType={'text'} thousandSeparator={true} prefix={''} suffix ={''}/></b></div>
              <div><NumberFormat value={plusUpKitNeed} displayType={'text'} thousandSeparator={true} prefix={'(: '} suffix ={')'}/></div>
            </div>
            <div className={cx('kit-use-f')} onClick={plusUpGoF} tabIndex='2' onKeyPress={plusUpGoKeyF}>
              <div> </div>
              <img src='/resource/img/plusupkitf.gif' alt=''/>
              <div><b><NumberFormat value={usePlusUpKitF} displayType={'text'} thousandSeparator={true} prefix={''} suffix ={''}/></b></div>
              <div><NumberFormat value={plusUpKitFNeed} displayType={'text'} thousandSeparator={true} prefix={'(: '} suffix ={')'}/></div>
            </div>
          </div>
          <div className={cx('plusup-body')}>
            <div className={cx('plusup-grade')}>:{simulCurrPlusUp}</div>
            <div className={cx('plusup-dmg')}>:{currPlusDmg}%</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PlusUpSimul;